import React from 'react';
import { useCart } from "react-use-cart";
import {SignUp} from "./Signup.js";
import {useState} from 'react';


const Cart = ( ) => {
    const [show, setShow] = useState(false);
    const { isEmpty, totalUniqueItems,items,totalItems,updateItemQuantity,removeItem,emptyCart,cartTotal } = useCart();
    if (isEmpty) return <h1 className="text-center">Korisi on nyt tyhjä</h1>
    return (
        <section className="py-4 container">
        <div className="row justify-content-center">
            <div className="col-12">

                <h5>Ostostkori ({totalUniqueItems}),Tuotteet yhteensä: ({totalItems})</h5>
                
                <table className="table table-light table-hover m-0">
                    <tbody>
                    {items.map((item, index)=>{
                        return( 

                        <tr key={index}>
                            <td>
                                <img src={item.img} style={{height: '6rem'}} />
                                <td>{item.title}</td>
                                <td>€{item.price}</td>
                                <td>Määrä ({item.quantity})</td>
                                <td>

                                    <button 
                                    className="btn btn-info ms-2"
                                    onClick={() => updateItemQuantity(item.id, item.quantity -1)}
                                    >-</button>

                                    <button className="btn btn-info ms-2"
                                    className="btn btn-info ms-2"
                                    onClick={() => updateItemQuantity(item.id, item.quantity +1)}
                                    >+</button>

                                    <button className="btn btn-danger ms-2"
                                    className="btn btn-info ms-2"
                                    onClick={() => removeItem(item.id)}
                                    >Poista tuote</button>

                                </td>
                            </td>
                        </tr>
                        )
                    })}
                    </tbody>
                </table>
            </div>

            <div className="col-auto ms-auto">
                <h2>Kokonaishinta: € {cartTotal}</h2>
            </div>

            <div className="col-auto">
                <button className="btn btn-danger m-2"
                onClick={() => emptyCart ()}
                > Tyhjennä ostoskorin</button>


<button className="btn btn-primary" onClick={()=>setShow(!show)}>{show ? "Vahvistus käynnissä":"Vahvista tilaus"}</button>
      {show &&
      <SignUp />
      }


            </div>
        </div>
        </section>
    )
}

export default Cart;