import React from 'react';
import '../App.css';
import {useState} from 'react';
import { useCart } from "react-use-cart";

const CartItem = ({item}) => {
    return (
        <div className="siuu">
        <p>Tuote:{item.title}</p>
        <p>Tuote per hinta:€{item.price}</p>
        <p>Määrä:{item.quantity}</p>
        </div>
    )
}


const CartItems = ({itemsData}) =>{
    return (
        <div>
           {itemsData.map(c => <CartItem key={c.id} item={c}/>)}
        </div>
    )
}


const Summary = ({clientinfo}) => {
const { items } = useCart();
const { inCart } = useCart();
console.log(inCart)
const { cartTotal, emptyCart } = useCart();

    return (
    <div>
        <div className="wrapper2">
                    <div className="title2">
                        Yhteenveto
                        <div className="form2">
                
                                <div className="input_field2">

                                    Tilatut tuotteet:<CartItems itemsData={items}/>
                                    Kokonaishinta: € {cartTotal}

                                    <p>katuosoite:{clientinfo.osoite}</p>

                                    <br/>
                                    Kiitos tilauksesta {clientinfo.name}!
                                    <br/><br/>

                                    Tiedot ovat lähetetty.
                                    <br/>
                                    <br/>
                                    <button type="submit2"
                                    onClick={() => emptyCart ()}
                                    >ok</button><br/>
                                
                                </div>
                        </div>
                    </div>
            </div>
    </div>
    );
};


const SignUp = () => {
    const [name,setName] = useState("");
    const [email,setEmail] = useState("");
    const [nro,setNro] = useState("");
    const [osoite,setOsoite] = useState("");
    const [pnro,setPnro] = useState("");
    const [toimipaik,setToimipaik] = useState("");
    const [show, setShow] = useState(false);

    const [clientinfo, setClientinfo] = useState({ name:"", email:"", nro:"", osoite:"", pnro:"", toimipaik:"" });

    const makeclient = (e) =>  {
        e.preventDefault()
        setClientinfo(  { name:name, email:email, nro:nro, osoite:osoite, pnro:pnro, toimipaik:toimipaik })
    }

    console.log(clientinfo)
    return (
        
        <div >
            <div>
            <form onSubmit={e=>makeclient(e)}>
                                <div className="wrapper">
                        <div className="title">
                            Yhteystiedot
                            <div className="form">

                                        <div className="input_field">
                                        <input type="text" placeholder="Name" value={name} onChange={e=>setName(e.target.value)} className="input"/><br/>
                                        <br/>
                                        <input type="text" placeholder="Email" value={email} onChange={e=>setEmail(e.target.value)} className="input"/><br/>
                                        <br/>
                                        <input type="number" placeholder="number" value={nro} onChange={e=>setNro(e.target.value)} className="input"/>
                                        Toimitus paikka <br></br>
                                        <input type="text" placeholder="katuosoite" value={osoite} onChange={e=>setOsoite(e.target.value)} className="input"></input><br/>
                                        <br/>
                                        <input type="number" placeholder="postinumero" value={pnro} onChange={e=>setPnro(e.target.value)} className="input"/><br/>
                                        <br/>
                                        <input type="text" placeholder="postitoimipaikka" value={toimipaik} onChange={e=>setToimipaik(e.target.value)} className="input"/><br/>
                                        <br/> 
                                        <button className="btn btn-primary" onClick={()=>setShow(!show)}>{show ? "Vahvistus käynnissä":"Lähetä"}</button>
                                        {show &&
                                        <Summary clientinfo={clientinfo}/>
                                        }

                                        </div>
                                </div>
                        </div>
                    </div>
            </form>
            </div>
        </div>
    );
};

export  {SignUp, Summary};


